import React, {Component, Fragment} from 'react';
import {withRouter,Link, Route } from 'react-router-dom';
import { connect } from 'react-redux'
import * as actions from "../../actions";

import MetisMenu from 'react-metismenu';

import {MainNav} from './NavItems';

class Nav extends Component {

    state = {
        activeItem: null
    };

    render() {

        const { history,selectedItem } = this.props
        const { activeItem } = this.state
        return (
            <Fragment>
                <h5 className="app-sidebar__heading">Menus</h5>
                <MetisMenu onSelected={(e) => {
                    e.preventDefault()
                    const linkHref = e.target.getAttribute('href')
                    this.props.change_active_nav_tab(null)
                    history.push(linkHref)
                    this.setState({
                        activeItem: linkHref
                    })
                }} content={MainNav} activeLinkFromLocation  activeLinkTo = { selectedItem !== null && selectedItem ? selectedItem : activeItem } className="vertical-nav-menu" iconNamePrefix="" classNameStateIcon="pe-7s-angle-down"/>
            </Fragment>
        );
    }

    isPathActive(path) {
        return this.props.location.pathname.startsWith(path);
    }
}

const mapStateToProps = (state) => {

    return {
        selectedItem:  state.ThemeOptions.navItem
    }
}

export default withRouter(connect(mapStateToProps,actions)(Nav));
